<template>
    <div>
        <div class="table-responsive">
            <table class="table">
                <thead class="thead-light">
                <tr>
                    <th scope="col"></th>
                    <th scope="col" class="pl-0">商品</th>
                    <th scope="col" class="pl-0">價格</th>
                    <th scope="col" class="pl-0">數量</th>
                    <th scope="col" class="pl-0">小計</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item, index) in order.products" :key="index">
                    <td class="p-0">
                    <img
                        :src="item.product.imageUrl"
                        style=" max-width:50px;display: block;
                            margin-left: auto;
                            margin-right: auto;"
                    />
                    </td>
                    <td class="text-left px-0 pt-4">{{item.product.title}}</td>
                    <td class="text-left px-0 pt-4 ntDollar">NT${{item.product.price}}</td>
                    <td class="text-left px-0 pt-4">{{item.qty}}</td>
                    <td class="text-left px-0 pt-4 ntDollar d-flex justify-content-between">
                        <span>NT${{item.final_total}}</span>
                        <span v-if="item.final_total != item.total" class="text-success pr-1">已使用優惠券</span>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        <div class="row justify-content-end">
            <div class="col-6">
                <div class="table-responsive">
                <table class="table">
                    <thead class="thead-light">
                    <tr>
                        <th class="text-center" colspan="2">訂單總計</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>小計</td>
                        <td class="ntDollar pl-1">NT${{order.total}}</td>
                    </tr>
                    </tbody>
                    <tfoot>
                    <tr>
                        <td colspan="2">
                        <button v-if="!order.is_paid" class="btn btn-outline-primary w-100 gopaid" style="font-size:5px" @click.prevent="gopaiding(order.id)">
                            前往結帳
                        </button>
                        </td>
                    </tr>
                    </tfoot>
                </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            order: {},
            isLoading: false,
        }
    },
    created() {
        this.getOneOrder(this.$route.params.orderId)
    },
    methods: {
        getOneOrder(id) {
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/order/${id}`;
            console.log("begin to get one order")
            console.log(api)
            var vm = this;
            vm.isLoading = true
            vm.$http.get(api).then(response => {
                console.log("response data")
                console.log(response)
                if (response.data.success) {
                    vm.order = response.data.order
                } else {
                    console.log("get one order failed")
                }
                vm.isLoading = false;
            })
        },
        gopaiding(id) {
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/pay/${id}`;
            console.log("begin to get one order")
            console.log(api)
            var vm = this;
            vm.isLoading = true
            vm.$http.post(api).then(response => {
                console.log("reponse data")
                console.log(response)
                if (response.data.success) {
                    console.log(response.data.message);
                    console.log("finish paying for id : " + id);
                    vm.$router.push('/customer_order/paidList')
                } else {
                    console.log("fail paying for id : " + id);
                }
            })
            
        }
    },
}
</script>

<style scoped>
.ntDollar {
  color: #b0aa95;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
}

.btn.btn-outline-primary.gopaid {
  color: white;
  background-color: #b0aa95;
  border-color:#b0aa95;
  border-radius: 0%;
}
.btn.btn-outline-primary.gopaid:hover {
  background-color: #666;
  border-color:#666;
}
</style>